<template>
      <div :class="['dropdown', {'show' : isShowMenu}]">
  <div :class="['dropbtn', {'active': activeIndx == index && isShowSubMenu}]" v-for="(link, index) in NAV_TITLES" :key="`${link}_${index}`"
  @click="showSubMenu(link, index)">
  <img v-if="index === 2" class="logo" :src="logo" alt="" @click="$router.push({ name: 'main' })">{{link}}
    <div :class="['dropdown-content', {'active':  activeIndx == index && isShowSubMenu}]">  
        <span v-for="(item, index) in NAV_LINKS" :key="`${item}_${index}`"
        @click="$router.push({ path: '/article' })"
        >{{item.name}}</span>
    </div>
 

    <!-- <li>КРАСОТА</li>
    <li>КАРЬЕРА</li>

    <li>ДУША</li>
    <li>ИНТИМНОЕ</li> -->

  </div>
  </div>
</template>


<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'MainPage',
    props: {
    isShowMenu: {
        type: Boolean,
    }
    },
    data: () => ({
      logo: require('../assets/img/mainlogo.png'),
      wom: require("../assets/img/woman.png"),
    //   isShowMenu: false,
      isShowSubMenu: false,
      activeIndx: null,
    }),
    computed: {
    ...mapGetters(['NAV_LINKS', 'NAV_TITLES']),
    },
    mounted () {
},
    methods: {
    ...mapMutations(['CHANGE_CURRENT_MENU', 'CHANGE_CURRENT_TITLE']),
        showSubMenu(link, index) {
          this.activeIndx = this.activeIndx === index ? this.activeIndx : index
          this.isShowSubMenu = !this.isShowSubMenu
          this.CHANGE_CURRENT_MENU(link)
        },
        hideSubMenu() {
      this.activeIndx = null

        }
    }
}
</script>